/* Social hint dimensions */
.button {
  display: inline-block;
  padding: 0.7rem 1.3rem;
  margin-right: 16px;
  margin-bottom: 16px;
  background-color: #cccccc;
  border: 1px solid #cccccc;
  color: #fff;
  line-height: 1;
  font-size: 18px;
  font-weight: 400;
  text-decoration: none; }

.button--primary {
  border: 1px solid #1184FE;
  background-color: #1184FE;
  color: #fff; }

.button--green {
  border: 1px solid #2dce00;
  background-color: #2dce00;
  color: #fff; }

.button--outline {
  background: transparent;
  color: #666; }

.button--text {
  background-color: transparent;
  border: 0;
  color: inherit;
  font-weight: 300;
  padding: 0.5rem 0.7rem 0.6rem; }

.button--text.button--green {
  color: #2dce00; }

.button__icon {
  max-width: 18px;
  max-height: 18px;
  margin-bottom: -2px;
  margin-left: 10px; }

.button__icon--left {
  max-width: 26px;
  max-height: 26px;
  margin-bottom: -7px;
  margin-right: 5px; }

.button__icon--back {
  max-width: 26px;
  max-height: 26px;
  margin-bottom: -2px;
  margin-right: 5px; }

.button__icon--dimmed {
  opacity: .3; }

.button--block {
  display: block; }

.button--margin-bottom {
  margin-bottom: 3rem; }

.button--show-more {
  cursor: pointer; }

.button--loading {
  transition: opacity 0.3s;
  opacity: 0.6;
  animation: pulse 0.4s infinite; }

@keyframes pulse {
  50% {
    transform: scale(0.94, 0.94); } }

/*
 *
 * Button action effects
 *
*/
.button {
  transition: all 0.16s ease-in; }
  .button:hover {
    background: #bdbdbd; }
  .button.button--primary:hover {
    background: #0175ef; }
  .button.button--green:hover {
    background: #2abf00; }
  .button.button--outline:hover {
    background: rgba(0, 0, 0, 0.04); }
  .button.button--text:hover {
    background: transparent;
    padding-left: 1rem; }
